window.addEventListener("load",function(e){let n=new WebSocket("ws://localhost:8887/");n.addEventListener("open",function(e){console.log("Connecté au serveur websocket TDC")}),n.addEventListener("message",function(e){100===JSON.parse(e.data).code&&(console.log("ping !!!"),alert("Page rechargée par ping"))}),n.addEventListener("close",function(e){console.log("Connexion avec le serveur websocket TDC interrompue.")})});